#loginContainer{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

    #loginHeader{
        height: 50px;
        background-color: darkslateblue;
    }

    #loginMain{
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        overflow: scroll;
        background-color: darkmagenta;
    }

        #loginMain0{
            width: 100%;
            height: 250px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
        }

            .loginMain0{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }

        #loginMain1{
            width: 100%;
            height: 25px;
            display: flex;
        }

            .loginMain1{
                flex: 1;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: darkmagenta;
            }

        #loginMain2{
            width: 100%;
            height: 200px;
            display: flex;
            flex-direction: column;
        }

            .loginMain2{
                flex: 1;
                background-color: darkmagenta;
            }

        #loginMain3{
            width: 100%;
            height: 200px;
            display: flex;
            flex-direction: column;
        }

            .loginMain3{
                flex: 1;
                background-color: darkmagenta;
            }

        #loginMainNull{
            display: none;
        }

    #loginFooter{
        height: 50px;
        background-color: darkslateblue;
    }