#sideContainer{
    width: 100vw;
    height: 100vh;
    height: -webkit-fill-available;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    overflow: hidden;
    position: absolute;
}

#sideNull{
    display: none;
}

    #sideHeader{
        width: 250px;
        height: 50px;
        background-color: darkslateblue;
    }

    #sideMain{
        width: 250px;
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        overflow: scroll;
        background-color: darkmagenta;
    }

        #sideMain0{
            width: 100%;
            height: 200px;
            display: flex;
            flex-direction: column;
        }

            .sideMain0{
                width: 100%;
                flex: 1;
                display: flex;
                align-items: center;
            }

            .sideMain0Img{
                width: 100px;
                height: 100px;
                position: relative;
                border-radius: 200px;
                object-fit: cover;
            }

        #sideMain1{
            width: 100%;
            height: 800px;
        }

    #sideFooter{
        width: 250px;
        height: 50px;
        background-color: darkslateblue;
    }

    #sideEmpty{
        width: 100%;
        height: 100%;
        background-color: black;
        opacity: 50%;
    }