html,body{
    width: 100vw;
    height: 100vh;
    height: -webkit-fill-available;
    display: flex;
    padding: 0;
    margin: 0;
    font-size: 17.5px;
    font-family: 'Reem Kufi', sans-serif;
}

#root{
    flex: 1;
    display: flex;
}

img{
    width: 65%;
    height: 65%;
    object-fit: contain;
    position: absolute;
}

button{
    padding: 0;
    margin: 0;
    border-style: none;
    color: black;
    font-family: 'Reem Kufi', sans-serif;
    font-size: 17.5px;
}

a{
    text-decoration: none;
    color: black;
}

input{
    border-style: none;
    font-family: 'Reem Kufi', sans-serif;
    font-size: 17.5px;
}

::placeholder{
    color: black;
    font-size: 17.5px;
}