#editContainer{
    flex: 1;
    display: flex;
    flex-direction: column;
}

    #editHeader{
        height: 50px;
        background-color: darkslateblue;
    }

    #editMain{
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        overflow: scroll;
        background-color: darkmagenta;
    }

        #editMain0{
            width: 100%;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        
        #editMain1{
            width: 100%;
            height: 200px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
        }

            .editMain1{
                width: 150px;
                height: 150px;
                object-fit: cover;
                border-radius: 200px;
            }

        #editMain2{
            width: 100%;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: darkmagenta;
        }
    

    #editFooter{
        height: 50px;
        background-color: darkslateblue;
    }